import React, { useState, useEffect } from 'react';

import Cart from '../../assets/cart.png'
import Gama from '../../assets/gama.png'

import { Container, Footers } from './style';

import api from '../../services/api';

interface IProduct{
  id: number;
  photo: string;
  name: string;
  description: string;
  price: number;
}

interface Test {
  cart: IProduct[]
}

const Home: React.FC = () => {
  const [ data, setData ] = useState<IProduct[]>([]);
  const [ cart, setCart ] = useState<IProduct[]>([]);
  

  useEffect(() =>{
    api.get('').then(
      response => {
        setData(response.data)
      }
    )
  }, [])

  const handleCart = (index: number) => {
    let product = data[index]
    let push: any = [...cart, cart.push(product)]
    setCart(push)
    const productStore = JSON.stringify(cart);
    localStorage.setItem('@cart', productStore)
  }

  return( 
    <>
      <Container>
        <div className="nav">
          <div>
            <img src="data:image/png;base64," alt="vtex" width="200px" height="auto" />
          </div>
          <div className="cart">
            <img src={Cart} alt="shopcart" width="50px" height="auto" />
            <span>( {cart.length} ) - Itens</span>
          </div>
        </div>
        <section>
          { data.map( (prod, index) => (
            <div className="product-content" key={prod.id}>
              <img className="product-img" src={prod.photo} alt="iphone" width="auto" height="150" />
              <h4>{prod.name}</h4>
              <span>{prod.description}</span>
              <h6>{prod.price}</h6>
              <button className="adicionar" onClick={ () => handleCart(index)}> Adicionar ao carrinho</button>
            </div>
          ))}
        </section>
        
      </Container>
      <Footers>
        <footer className="footer">
          <p>Feito  para</p>
          <img id="gama" src={Gama} alt="Gama Academy" />
        </footer>
      </Footers>
    </>
  );
}

export default Home;